@import "_config.scss";
$color: #001f3f;
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media screen and (min-width: 320px) and (max-width: 480px) {
  .aboutMe {
    .intro {
      h1 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 4.083vw;
        margin-bottom: 0;
        margin-top: 2.389vw;
      }
      /* necessary for mobile */
      .aboutme1 {
        position: absolute;
        top: 12%;
        padding-right: 2.2vw;
        padding-left: 2.2vw;
      }
      position: absolute;
      top: 15%;
      left: 5.5%;
      width: 90%;
      height: 50vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
      overflow-y: scroll;
    }
    .skillsets {
      h4 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 4.083vw;
        margin-bottom: 1vh;
        // margin-top: 3.389vw;
      }
      .Course{
        display: none;
      }
      .ProgLang, .Frame, .Hardware {
        h2 {
          font-size: 9px;
          font-family: "Lato", sans-serif;
          padding: 0;
          margin-top: 0;
        }  
        line-height: 10px;
        .new-list{
          max-width: 100px;
          ul,li{
            list-style-type: none;
            display: inline-block;
            text-align: left;
            font-family: 'Abel', sans-serif;
            color: white;
            z-index: 999;
            font-size: 8px;
            padding-right: 3px;
            padding-left: 3px;
            margin-right: 2px;
            margin-bottom: 5px;
            border-radius: 3px;
          }
          li{
            text-align: center;
            min-width: 12px;
            background-color: rgb(245, 181, 53);
            text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
          }
        }
        display: flex;
        flex-direction: column;
        margin-left: 4vw;
      }
      .containerr{
        padding: 0;
        display: flex;
        flex-direction: row;
      }
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 68%;
      left: 5.5%;
      width: 90%;
      height: 20vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
    }
    a {
      color: $color;
      text-decoration: underline;
      text-align: center;
    }
    p {
      padding: 0;
      font-family: "Lato", sans-serif;
      line-height: 5.778vw;
      font-size: 2.881vw;
      em {
        color: $color;
        text-emphasis-style: none;
        font-style: normal;
        font-weight: bold;
      }
    }
    overflow-y: hidden;
    overflow-x: hidden;
    position: -webkit-sticky;
    position: sticky;
    color: $color;
    left: 14.583vw;
    right: 30%;
    width: 100%;
    line-height: 3.472vw;
    height: 100vh;
  }
}

/* 
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/
@media screen and (min-width: 1025px) {
  .aboutMe {
    /* A brief introduction */
    .intro {
      h1 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 2.083vw;
        margin-bottom: 1.389vw;
        margin-top: 1.389vw;
      }
      width: 50%;
      height: 81vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
      display: flex;
      padding: 20px;
      margin-right: 50px;
      flex-direction: column;
      overflow-y: scroll;
    }
    .skillsets {
      padding: 20px;
      h4 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 2.083vw;
        margin-top: 1.389vw;
      }
      .ProgLang, .Frame, .Hardware, .Course {
        h2 {
          font-size: 1.319vw;
          font-family: "Lato", sans-serif;
          display: inline;
          margin: 10px;
        }  
        line-height: 17em; //controls thickness of the tags
        .new-list{
          max-width: 280px;
          ul,li{
            list-style-type: none;
            display: inline-block;
            text-align: center;
            font-family: 'Abel', sans-serif;
            color: white;
            z-index: 999;
            font-size: 14px;
            padding-right: 4px;
            padding-left: 4px;
            margin-right: 5px;
            margin-bottom: 5px;
            border-radius: 6px;
          }
          li{
            text-align: center;
            min-width: 30px;
            background-color: rgb(245, 181, 53);
            text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
          }
          margin-right: auto;
          margin-left: auto;
        }
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      display: flex;
      flex-direction: column;
      width: 35%;
      height: 81vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
      overflow-y: scroll;
    }
    a {
      color: $color;
      text-decoration: underline;
      text-align: center;
    }
    p {
      padding: 0;
      font-family: "Lato", sans-serif;
      line-height: 2.778vw;
      font-size: 1.181vw;
      em {
        color: $color;
        text-emphasis-style: none;
        font-style: normal;
        font-weight: bold;
      }
    }
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: $color;
    left: 14.583vw;
    right: 30%;
    width: 80%;
    height: 100vh;
    padding: 20px;
    line-height: 3.472vw;
  }
}

