/// Convert a direction to legacy syntax
/// @param {Keyword | Angle} $value - Value to convert
/// @require {function} is-direction
/// @require {function} convert-angle
/// @throw Cannot convert `#{$value}` to legacy syntax because it doesn't seem to be a direction.;
@function legacy-direction($value) {
  @if is-direction($value) == false {
    @error "Cannot convert `#{$value}` to legacy syntax because it doesn't seem to be a direction.";
  }

  $conversion-map: (
    to top: bottom,
    to top right: bottom left,
    to right top: left bottom,
    to right: left,
    to bottom right: top left,
    to right bottom: left top,
    to bottom: top,
    to bottom left: top right,
    to left bottom: right top,
    to left: right,
    to left top: right bottom,
    to top left: bottom right
  );

  @if map-has-key($conversion-map, $value) {
    @return map-get($conversion-map, $value);
  }

  @return 90deg - $value;
}

/// Test if `$value` is a valid direction
/// @param {*} $value - Value to test
/// @return {Bool}
@function is-direction($value) {
  $is-keyword: index(
    (
      to top,
      to top right,
      to right top,
      to right,
      to bottom right,
      to right bottom,
      to bottom,
      to bottom left,
      to left bottom,
      to left,
      to left top,
      to top left
    ),
    $value
  );
  $is-angle: type-of($value) == "number" and
    index("deg" "grad" "turn" "rad", unit($value));

  @return $is-keyword or $is-angle;
}

@mixin linear-gradient($direction, $color-stops...) {
  // Direction has been omitted and happens to be a color-stop
  @if is-direction($direction) == false {
    $color-stops: $direction, $color-stops;
    $direction: 180deg;
  }

  background: nth(nth($color-stops, 1), 1);
  background: -webkit-linear-gradient(
    legacy-direction($direction),
    $color-stops
  );
  background: linear-gradient($direction, $color-stops);
}

@mixin translateY($e) {
  -webkit-transform: translateY($e);
  -moz-transform: translateY($e);
  transform: translateY($e);
}

@mixin translateX($e) {
  -webkit-transform: translateX($e);
  -moz-transform: translateX($e);
  transform: translateX($e);
}

@mixin transition($e) {
  -webkit-transition: $e;
  -moz-transition: $e;
  transition: $e;
}

body {
  position: static;
  //font-family: 'Open Sans', sans-serif;
  background: #31b7d7;
  font-size: 1.25px;
  color: $white;
  font-weight: 400;
  font-style: normal;
}
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media screen and (min-width: 320px) and (max-width: 480px) {
  .Homepage {
    @include linear-gradient(#edac7d, #31b7d7);
    background: #edac7d;
    background: -webkit-linear-gradient(-90deg, #edac7d, #31b7d7);
    background: linear-gradient(180deg, #edac7d, #31b7d7);
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    //when screen is beyond 1000px
    display: flex;
    flex-direction: column;
    .container {
      width: 100%;
      // border: 5px solid black;
      margin-top: 20vh;
      .Photo {
        text-align: center;
        img {
          height: 70vw;
          border-radius: 50%;
          border: 3px solid white;
        }
      }
      .titleContainer {
        h1 {
          //ucla cs 2022
          font-size: 8vw;
          font-family: "Quicksand", sans-serif;
          color: $white;
        }
        text-align: center;
      }
      h2 {
        .dash {
          display: none;
        }
        text-align: center;
        font-size: 5vw;
        font-family: "Quicksand", sans-serif;
        color: $white;
      }
      .LogoSets2 {
        font-size: 1.4vw;
        text-align: center;
      }
      .Arrow {
        margin-top: 7vh;
        text-align: center;
      }
    }
  }
}
/* 
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/
@media screen and (min-width: 1025px) {
