@import "_config.scss";

$color: #001f3f;

@media screen and (max-width: 500px) {
  footer {
    position: fixed;
    bottom: 0px;
    right: 0px;
    padding: 5px;
    font-family: 'PT Sans', sans-serif;
    em{
      font-size: 11px;
    }
    background-color: rgba(4, 6, 70, 0.6);
    color: $white;
    font-size: 9px;
  }
}

@media screen and (min-width: 500px) {
  footer {
    position: fixed;
    bottom: 0px;
    right: 0px;
    padding: 5px;
    font-family: 'PT Sans', sans-serif;
    em{
      font-size: 17px;
    }
    background-color: rgba(4, 6, 70, 0.6);
    color: $white;
    font-size: 13px;
  }
}


.Arrow2 {
  position: absolute;
  left: 48%;
  top: 48.5%;
}

.Arrow3 {
  position: absolute;
  left: 48%;
  top: 71%;
}

/* 
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/
@media screen and (min-width: 1025px) {
  header {
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 12.889vw;
    height: 100%;
    top: 0;
    left: 0;
    .logo {
      color: $white;
      font-family: "Poiret One", cursive;
      font-size: 2.917vw;
      line-height: 2.778vw;
      margin: 1vw;
      margin-bottom: 0;
      a {
        text-decoration: none;
        @include transition(0.4s ease-in-out);
        cursor: pointer;
        color: $white;
        &:link {
          color: $white;
        }
        &:visited {
          color: $white;
        }
        &:hover {
          color: $test;
          @include transition(0.4s ease-in-out);
        }
      }
    }

    .LogoSets {
      font-size: 2.8vw;
      margin-top: 10vh;
      margin-left: 8px;
    }
    nav,
    nav ul,
    nav ul li {
      margin: 0;
      padding: 0;
    }
    .navbar {
      margin-top: 3vh;
      font-size: 1.319vw;
      font-family: "Raleway", sans-serif;
      line-height: normal;
      color: $white;
      list-style-type: none;
      height: 80%;
      width: 100%;
      ul, li {
        list-style-type: none;
        margin-bottom: 1.042vw;
        padding: 0;
        margin-left: 1vw;
      }
      a {
        text-decoration: none;
        @include transition(0.4s ease-in-out);
        cursor: pointer;
        color: $white;
        &:link {
          color: $white;
        }
        &:visited {
          color: $white;
        }
        &:hover {
          color: $test;
          @include transition(0.4s ease-in-out);
        }
      }
      .explist {
        padding: 0;
        font-size: 1.042vw;
        font-family: "Raleway", sans-serif;
        line-height: normal;
        color: $white;
        margin-left: 0;
        margin-top: 0.417vw;
      }
    }
  }
}

/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media screen and (min-width: 320px) and (max-width: 480px) {
  header {
    z-index: 999;
    position: fixed;
    display: block;
    background: rgba(0, 0, 0, 0.2);
    width: 100%;
    height: 11%;
    top: 0;
    left: 0;
    .logo {
      top: 1vh;
      left: 1.5vw;
      color: $white;
      font-family: "Poiret One", cursive;
      font-size: 8vw;
      line-height: 8vw;
      position: absolute;
      width: 13.889vw;
      a {
        text-decoration: none;
        @include transition(0.4s ease-in-out);
        cursor: pointer;
        color: $white;
        &:link {
          color: $white;
        }
        &:visited {
          color: $white;
        }
        &:hover {
          color: $test;
          @include transition(0.4s ease-in-out);
        }
      }
    }
    .LogoSets {
      font-size: 1.4vw;
      position: absolute;
      top: 3%;
      right: 3vw;
      display: none;
    }
    nav,
    nav ul,
    nav ul li {
      margin: 0;
      padding: 0;
    }
    .navbar {
      position: absolute;
      top: 2.5vh;
      left: 20vw;
      font-size: 4.319vw;
      font-family: "Raleway", sans-serif;
      line-height: normal;
      color: $white;
      ul li {
        list-style-type: none;
        display: inline-block;
        margin-right: 2vw;
        margin-bottom: 1.042vw;
        padding: 0;
      }
      a {
        text-decoration: none;
        @include transition(0.4s ease-in-out);
        cursor: pointer;
        color: $white;
        &:link {
          color: $white;
        }
        &:visited {
          color: $white;
        }
        &:hover {
          color: $test;
          @include transition(0.4s ease-in-out);
        }
      }
      .explist {
        padding: 0;
        font-size: 1.042vw;
        font-family: "Raleway", sans-serif;
        line-height: normal;
        color: $white;
        margin-left: 0;
        margin-top: 0.417vw;
        padding-left: 0.694vw;
      }
    }
  }
}

